'use strict';
import {
  loadMoreFunc,
  page,
  loadMoreReset,
  loadMore
} from './loadMore.js';
import refs from '../index.js';
import {
  showPromo,
  apiData
} from './showPromo.js';
import apiCreator from './apiCreator.js';
import preloader from './preloader.js';
console.log('refs :', refs);



const filmSearcher = (event) => {
  console.log('event :', event);
  if (event !== undefined) {
    event.preventdefault;
    loadMoreReset();
    console.log('event.target :', event.target);
    loadMoreFunc(filmSearcher);
  }
  //значение поиска
  let query = refs.film_search_form.firstElementChild.value;

  //проверка на то, есть ли теги с фильмами в dom
  if (apiData.total_results !== 0 && !apiData.hasOwnProperty('errors') && page === 1) {
    refs.film_promo.forEach(promo => {
      refs.films_block.removeChild(promo);
    });
  }
  //
  showPromo(apiCreator.search(page, query));
}

export default filmSearcher;
