'use strict';
import refs from '../index.js';
import apiCreator from './apiCreator.js';
import filmSearcher from './filmSearcher.js';
import preloader from './preloader.js';
import {
  apiData
} from './showPromo.js';

let page = 1;
let apiFunc = preloader;// переменная функции, которая сюда передается(preloader по умолчанию т.к. она выполняется первой)

const loadMore = () => {
  page += 1;
  if (page >= apiData.total_pages || apiData.hasOwnProperty('errors')) {
    refs.load_more_btn.style.display = 'none';
  }
  apiFunc();
  return page;
}



const loadMoreReset = () => {
  page = 1;
  refs.load_more_btn.style.display = 'block';
}

//функция, которая возвращает, какую функцию использовать для кнопки (loadMore)
const loadMoreFunc = (func) => {
  apiFunc = func;

}
//



export {
  loadMoreFunc,
  loadMore,
  page,
  loadMoreReset
};
