'use strict';
import refs from '../index.js';
import {showPromo, apiData} from './showPromo.js';
import {loadMore, page, loadMoreReset, loadMoreFunc} from './loadMore.js';
import apiCreator from './apiCreator.js';
console.log('refs :', refs);

const preloader = (event) => {
  if (event !== undefined) {
    event.preventdefault;
    loadMoreReset();
    loadMoreFunc(preloader);
    if (apiData.total_results !== 0 && !apiData.hasOwnProperty('errors') && page === 1) {
      refs.film_promo.forEach(promo => {
        refs.films_block.removeChild(promo);
      });
    }
  }
  showPromo(apiCreator.popular(page));
}
export default preloader;
